/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video, p {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.sideBar {
  position: fixed;
  min-width: 296px;
  min-height: 100vh;
  background-color: #F8F9FA; }
  .sideBarLogo {
    margin-top: 40px; }
    .sideBarLogo a {
      width: 151px;
      height: 40px;
      background-image: url("../images/logo-ANSWER.svg");
      background-size: cover;
      display: block;
      text-indent: 101%;
      overflow: hidden;
      white-space: nowrap; }
  .sideBarItem {
    display: flex;
    padding-top: 29px; }
    .sideBarItem span {
      font-size: 32px;
      padding-right: 18px; }
    .sideBarItem p {
      font-size: 16px;
      line-height: 24px;
      font-weight: bold;
      cursor: pointer; }
    .sideBarItem a {
      color: #000000;
      text-decoration: none; }
    .sideBarItem a:hover {
      color: #198754; }
    .sideBarItemActive {
      color: #198754; }
  .sideBarItem:hover {
    color: #198754; }
  .sideBarMember {
    display: flex;
    align-items: center;
    margin-bottom: 32px; }
  .sideBarImg {
    padding-right: 16px; }
