$font-size-base:16px;

$text-xxs: $font-size-base * 0.25; //4px
$text-xs: $font-size-base * 0.75; //12px
$text-s: $font-size-base * 0.875; //14px
$text-base: $font-size-base; 
$text-m: $font-size-base * 1.25; //20px
$text-l: $font-size-base * 1.5; //24px
$text-2l: $font-size-base * 1.75; //28px
$text-xl: $font-size-base * 2; //32px
$text-2xl: $font-size-base * 2.25; //36px
$text-3xl: $font-size-base * 2.625; //42px
$text-4xl: $font-size-base * 3; //48px

$black:#000000;
$white:#ffffff;
$gray:#F8F9FA;

$gray-100: #F8F9FA;
$gray-200: #CED4DA;
$gray-300: #6C757D;
$gray-400: #212529;

$primary:#198754;
$secondary: #212529;
$third: #CED4DA;

$font-family-lato:'Lato', sans-serif;
$font-family-roboto:'Roboto Mono', monospace;



$spacer: 1rem !default;
$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3,
) !default;